*,
*::before,
*::after {
  box-sizing: border-box;
}

.nav {
  list-style-type: none;
  width: 500px;
  display: flex;
  justify-content: space-evenly;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.todo-app-container {
  min-height: 50vh;
  background: #008080;
}

/* .container {
  margin: auto;
  padding-top: 24px;
  max-width: 48rem;
} */

.todo-app {
  margin: auto;
  margin-top: 30px;
  padding: 2rem;
  background: white;
  border-radius: 0.25rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  max-width: 32rem;
  color: #374151;
}

h2 {
  font-size: 24px;
  font-weight: bold;
}

.todo-input {
  width: 100%;
  border: none;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  padding: 14px;
  font-size: 16px;
  margin-top: 16px;
}

.todo-list {
  margin-top: 32px;
}

.todo-item-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
}

.todo-item {
  display: flex;
  align-items: center;
  flex: 1;
  font-size: 18px;
  margin-right: 24px;
}

.todo-item-label {
  margin-left: 16px;
}

.todo-item-input {
  margin-left: 8px;
  width: 100%;
  border: none;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  padding: 6px 8px;
  font-size: 18px;
}

.line-through {
  text-decoration: line-through;
}

.x-button {
  background: white;
  color: #6b7280;
  border: none;
  cursor: pointer;
}

.x-button:hover {
  color: #1f2937;
}

.x-button-icon {
  width: 1.5rem;
  height: 1.5rem;
}

.button {
  color: #6b7280;
  font-size: 14px;
  background: white;
  border: 1px solid lightgray;
  padding: 6px;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  border: 1px solid lightgray;
}

.filter-button {
  border: 1px solid white;
}

.filter-button-active {
  border: 1px solid lightgray;
}

.check-all-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #6b7280;
  margin-top: 22px;
  padding-top: 16px;
  border-top: 1px solid lightgray;
}

.other-buttons-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #6b7280;
  margin-top: 22px;
  padding-top: 16px;
  border-top: 1px solid lightgray;
}

.no-todos-container {
  width: 300px;
  margin: 30px auto;
}

.no-todos-container p {
  margin-top: 20px;
  text-align: center;
}

.name-container {
  margin-bottom: 40px;
}

.name-label {
  margin-top: 15px;
}

.toggles-container {
  margin: 20px 0;
}

nav {
  background: white;
  padding: 16px;
}

nav ul {
  display: flex;
  justify-content: center;
  margin: auto;
}

nav ul li:not(:first-child) {
  margin-left: 16px;
}

.active {
  font-weight: bold;
}

/* Transitions  */

.slide-vertical-enter {
  opacity: 0;
  transform: translateY(-20px);
}

.slide-vertical-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: all 300ms;
}

.slide-vertical-exit {
  opacity: 1;
  transform: translateY(0);
}

.slide-vertical-exit-active {
  opacity: 0;
  transition: all 300ms;
  transform: translateY(-20px);
}

.slide-horizontal-enter {
  opacity: 0;
  transform: translateX(20px);
}

.slide-horizontal-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: all 300ms;
}

.slide-horizontal-exit {
  opacity: 1;
  transform: translateX(0);
}

.slide-horizontal-exit-active {
  opacity: 0;
  transition: all 300ms;
  transform: translateX(20px);
}
